import React from "react";

import  Blake  from '../assets/bcut.png';
import { StyledHome } from '../styles';
import Animated from './Animated';
import Projects from './Projects';


function Home() {
  

  return (
    <>
      <StyledHome>
        <div className="bg-wtc">
          <div className="welcome-text-container">
            <div className="welcome-text">
              <div className="blake">

            <p className="name-text">Blake Prouty</p>
           
            <p className="animated-text">Full Stack Developer</p>
            
              </div>
          <div className="pic-center">
            <div className="pic-container">
                <img className="profile-pic" src={Blake} alt="profile" />
            </div>
            <div className="socials">
              <div className="contact">
                <div className="contact-info">
                <p>blake.n.prouty@gmail.com</p>
              </div>
              <div className="contact-info">
               <p>(605) 521-7234</p>
              </div>
              </div>
              <div className="media">
            <a
              href="https://www.github.com/Prouty89"
              target="_blank"
              rel="noopener noreferrer"
            >
              <img
                src="https://img.icons8.com/ios/45/36475d/github.png"
                alt="github"
              />
            </a>
            <a
              href="https://twitter.com/BlaketotheNth"
              target="_blank"
              rel="noopener noreferrer"
            >
              <img
                src="https://img.icons8.com/45/36475d/linkedin-circled.png"
                alt="linked"
              />
            </a>
            <a
              href="https://twitter.com/BlaketotheNth"
              target="_blank"
              rel="noopener noreferrer"
            >
              <img
                src="https://img.icons8.com/ios/45/36475d/twitter.png"
                alt="twitter"
              />
            </a>
            </div>
            </div>
          </div>
            </div>
          <div className="welcome-container">
            <Animated />
          </div>
          </div>
        </div>
        <div className="home-contents">
          <div className="projects-container">
            <Projects />
          </div>
        </div>
      </StyledHome>
    </>
  );
}

export default Home;