import React, { useState } from 'react';
import { useTransition } from 'react-spring';
import Styled, { keyframes } from 'styled-components';



import Pdf from '../assets/BlakeProuty_Resume.pdf';
import About from './About';
import Ed from './Ed';

function Animated() {
  
    //about
    const [modalVisible, setModalVisible] = useState(true);
    const transitions = useTransition(modalVisible, null, {
          from: { display: "none", transform: "translateY(80px)" },
          enter: { display: "flex", transform: "translateY(0px)" },
          leave: { display: "none", transform: "translateY(20px)" }
        });
    //ed
    const [eduModalVisible, setEduModalVisible] = useState(false);
    const edutransitions = useTransition(eduModalVisible, null, {
          from: { display: "none", transform: "translateY(80px)" },
          enter: { display: "flex", transform: "translateY(0px)" },
          leave: { display: "none", transform: "translateY(20px)" }
        });

    function closeOthersEd(){
      setEduModalVisible(true);
      setModalVisible(false);
    }

    function closeOthersAbout(){
      setEduModalVisible(false);
      setModalVisible(true);
    }

  const fill = keyframes`
  0% {
      background-image: linear-gradient(120deg, #fcfcfc 0%, #fcfcfc 100%);
      background-repeat: no-repeat;
      background-size: 10% 0.05em;
      background-position: 0 85%;
      transition: background-size 0.25s ease-in; 
  }
  100%{
      background-image: linear-gradient(120deg, #fcfcfc 0%, #fcfcfc 100%);
      background-repeat: no-repeat;
      background-size: 78% 0.05em;
      background-position: 50% 85%;
      transition: background-size 0.25s ease-in;
  }
`
    const StyledButton = Styled.div `
        .modal-active{
          font-family: 'Quicksand', sans-serif;
          color: #fcfcfc;
          background: #36475d;
          margin: 15px;
          font-size: 2em;
          box-shadow: inset 0px 0px 0px 2px #fcfcfc;
          display: flex;
          align-items: center;
          border: none;
          justify-content: center;
          cursor: pointer;
          min-width: 190px;
          max-width: 190px;
          text-align: left;
          border-radius: 3px;
          outline: none;
          user-select: none;
          height: 50px;
          margin-right: 15px;
          text-decoration: none;
          background-image: linear-gradient(120deg, #fcfcfc 0%, #fcfcfc 100%);
          background-repeat: no-repeat;
          background-size: 85% 0.05em;
          background-position: 50% 85%;

          @media(max-width: 950px) {
            font-size: 1.3rem;
            min-width: 135px;
            max-width: 135px;
          }

          @media(max-width: 600px) {
            font-size: .9rem;
            min-width: 85px;
            max-width: 85px;
          }
        }

        .modal-inactive{
          font-family: 'Quicksand', sans-serif;
          background: #526073;
          margin: 15px;
          box-shadow: inset 0px 0px 0px 2px #fcfcfc;
          font-size: 2em;
          color: #fcfcfc;
          outline: none;
          border-radius: 3px;
          justify-content: center;
          user-select: none;
          min-width: 190px;
          max-width: 190px;
          display: flex;
          align-items: center;
          border: none;
          cursor: pointer;
          text-align: left;
          margin-right: 15px;
          height: 50px;
          text-decoration: none;

          @media(max-width: 950px) {
            font-size: 1.3rem;
            min-width: 135px;
            max-width: 135px;
          }

          @media(max-width: 600px) {
            font-size: .9rem;
            min-width: 85px;
            max-width: 85px;
          }

          &:hover{
            animation-name: ${fill};
            animation-timing-function: ease;
            animation-duration: 400ms;
            animation-delay: 0s;
            animation-fill-mode: forwards;
          }
        }
          a{
          font-family: 'Quicksand', sans-serif;
          color: #fcfcfc;
          background: #526073;
          min-width: 190px;
          max-width: 190px;
          justify-content: center;
          margin: 15px;
          font-size: 2em;
          display: flex;
          border-radius: 3px;
          align-items: center;
          border: none;
          margin-right: 15px;
          cursor: pointer;
          text-align: left;
          height: 50px;
          
          box-shadow: inset 0px 0px 0px 2px #fcfcfc;
          text-decoration: none;

          @media(max-width: 950px) {
            font-size: 1.3rem;
            min-width: 135px;
            max-width: 135px;
          }

          @media(max-width: 600px) {
            font-size: .9rem;
            min-width: 85px;
            max-width: 85px;
          }

          &:hover{
            animation-name: ${fill};
            animation-timing-function: ease;
            animation-duration: 400ms;
            animation-delay: 0s;
            animation-fill-mode: forwards;
          }
        }

        
    `

    return (
    <>
    <div className="home-text-container">
    <StyledButton className="margin-btn">
    <button 
    className={modalVisible === true ? "modal-active about" : "modal-inactive about"}
    onClick={() => closeOthersAbout()}>
        About
    </button>
    </StyledButton>
<StyledButton>
      <button className={eduModalVisible === true ? "modal-active" : "modal-inactive"} onClick={() => closeOthersEd()}>
        Education
    </button>
    </StyledButton>
<StyledButton>
      <a className="modal-button" href={Pdf} target="_blank" rel="noopener noreferrer">
              Resume
      </a>
      </StyledButton>
    </div>
    <div className="modal-container">
    {transitions.map(
        ({ item, key, props: style }) =>
          item && (
            <About
              style={style}
              key={key}
            />
          )
      )}     
    {edutransitions.map(
        ({ item, key, props: style }) =>
          item && (
            <Ed
              style={style}
              key={key}
            />
          )
      )}
      </div>
    </>
    )
}

export default Animated;
