import React, { useState } from 'react';
import { useTransition } from 'react-spring';
import Styled, { keyframes } from 'styled-components';
import { StyledProjects } from '../styles';
import { 
  One,
  Two,
  Three,
  Four,
  Five,
  Six
} from './ProjectData';

const fill = keyframes`
  0% {
      background-image: linear-gradient(120deg, #526173 0%, #526173 100%);
      background-repeat: no-repeat;
      background-size: 10% 0.05em;
      background-position: 0 85%;
      transition: background-size 0.25s ease-in; 
  }
  100%{
      background-image: linear-gradient(120deg, #526173 0%, #526173 100%);
      background-repeat: no-repeat;
      background-size: 78% 0.05em;
      background-position: 50% 85%;
      transition: background-size 0.25s ease-in;
  }
`


const StyledButton = Styled.div `
display: flex;
align-items: center;
justify-content: center;
min-width: 210px;
padding: 5px;

@media(max-width: 750px){
  min-width: 160px;
}


.empty{
  width: 25px;
}

.modal-active{
  font-family: 'Quicksand', sans-serif;
  color: #526073;
  background: #fcfcfc;
  margin: 5px;
  font-size: 1.2em;
  box-shadow: inset 0px 0px 0px 2px #526173;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  justify-content: center;
  min-width: 165px;
  border: none;
  cursor: pointer;
  text-align: left;
  border-radius: 3px;
  outline: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  height: 45px;
  padding: 0px 12px;
  -webkit-text-decoration: none;
  text-decoration: none;
  background-image: linear-gradient(120deg, #526173 0%, #526173 100%);
  background-repeat: no-repeat;
  background-size: 85% 0.05em;
  background-position: 50% 85%;

  @media(max-width: 750px){
    height: 35px;
    min-width: 130px;
    font-size: .9em;
  }
}

.modal-inactive{
  font-family: 'Quicksand', sans-serif;
  background: #fcfcfc;
  margin: 5px;
  font-size: 1.2em;
  color: #526073;
  outline: none;
  padding: 0px 12px;
  border-radius: 3px;
  box-shadow: inset 0px 0px 0px 2px #526173;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  justify-content: center;
  min-width: 165px;
  border: none;
  cursor: pointer;
  text-align: left;
  height: 45px;
  -webkit-text-decoration: none;
  text-decoration: none;

  @media(max-width: 750px){
    height: 35px;
    min-width: 130px;
    font-size: .9em;
  }
  &:hover{
    animation-name: ${fill};
    animation-timing-function: ease;
    animation-duration: 400ms;
    animation-delay: 0s;
    animation-fill-mode: forwards;
  }
  }

}
`



function Projects() {
  
  
  const [oneVisible, setOneVisible] = useState(true);
  const transitions = useTransition(oneVisible, null, {
      from: { transform: 'translate3d(40px,0px,0)' },
      enter: { transform: 'translate3d(0,0px,0)' },
      leave: { transform: 'display: none' },
      });
  const [twoVisible, setTwoVisible] = useState(false);
  const twotransitions = useTransition(twoVisible, null, {
    from: { transform: 'translate3d(40px,0px,0)' },
    enter: { transform: 'translate3d(0,0px,0)' },
    leave: { transform: 'display: none' },
    });

  const [threeVisible, setThreeVisible] = useState(false);
  const threetransitions = useTransition(threeVisible, null, {
    from: { transform: 'translate3d(40px,0px,0)' },
    enter: { transform: 'translate3d(0,0px,0)' },
    leave: { transform: 'display: none' },
    });
    const [fourVisible, setFourVisible] = useState(false);
    const fourtransitions = useTransition(fourVisible, null, {
      from: { transform: 'translate3d(40px,0px,0)' },
      enter: { transform: 'translate3d(0,0px,0)' },
      leave: { transform: 'display: none' },
        });
    const [fiveVisible, setFiveVisible] = useState(false);
    const fivetransitions = useTransition(fiveVisible, null, {
      from: { transform: 'translate3d(40px,0px,0)' },
      enter: { transform: 'translate3d(0,0px,0)' },
      leave: { transform: 'display: none' },
      });
  
    const [sixVisible, setSixVisible] = useState(false);
    const sixtransitions = useTransition(sixVisible, null, {
      from: { transform: 'translate3d(40px,0px,0)' },
      enter: { transform: 'translate3d(0,0px,0)' },
      leave: { transform: 'display: none' },
      });



    function closeOthersOne(){
      setOneVisible(true);
      setTwoVisible(false);
      setThreeVisible(false);
      setFourVisible(false);
      setFiveVisible(false);
      setSixVisible(false);
    

    }

    function closeOthersTwo(){
      setOneVisible(false);
      setTwoVisible(true);
      setThreeVisible(false);
      setFourVisible(false);
      setFiveVisible(false);
      setSixVisible(false);
   
    }

    function closeOthersThree(){
      setOneVisible(false);
      setTwoVisible(false);
      setThreeVisible(true);
      setFourVisible(false);
      setFiveVisible(false);
      setSixVisible(false);
    }

    function closeOthersFour(){
      setOneVisible(false);
      setTwoVisible(false);
      setThreeVisible(false);
      setFourVisible(true);
      setFiveVisible(false);
      setSixVisible(false);
    }

    function closeOthersFive(){
      setOneVisible(false);
      setTwoVisible(false);
      setThreeVisible(false);
      setFourVisible(false);
      setFiveVisible(true);
      setSixVisible(false);
    }

    function closeOthersSix(){
      setOneVisible(false);
      setTwoVisible(false);
      setThreeVisible(false);
      setFourVisible(false);
      setFiveVisible(false);
      setSixVisible(true);

    }

    return (
      <>
      <div className="bg-ptc">
        <StyledProjects>
          <div className="btnsw-header">
          <p className="projects-header">My Work</p>
          <div className="btns-container">
          <div className="project-btns">
            <StyledButton>
              <button
                className={
                  oneVisible === true ? "modal-active" : "modal-inactive"
                }
                onClick={() => closeOthersOne()}
              >
                Charity Shop
              </button>
            </StyledButton>
            <StyledButton>
              <button
                className={
                  twoVisible === true ? "modal-active" : "modal-inactive"
                }
                onClick={() => closeOthersTwo()}
              >
                Riders 4 Life
              </button>
            </StyledButton>
            <StyledButton>
              <div></div>
              <button
                className={
                  threeVisible === true ? "modal-active" : "modal-inactive"
                }
                onClick={() => closeOthersThree()}
              >
                React Tetris
              </button>
            </StyledButton>
            <StyledButton>
              <button
                className={
                  fourVisible === true ? "modal-active" : "modal-inactive"
                }
                onClick={() => closeOthersFour()}
              >
                Cyber MUD
              </button>
            </StyledButton>
            <StyledButton>
              <button
                className={
                  fiveVisible === true ? "modal-active" : "modal-inactive"
                }
                onClick={() => closeOthersFive()}
              >
                Stock Messages
              </button>
            </StyledButton>
            <StyledButton>
              <button
                className={
                  sixVisible === true ? "modal-active" : "modal-inactive"
                }
                onClick={() => closeOthersSix()}
              >
                Github Finder
              </button>
            </StyledButton>
          </div>
          </div>
          </div>
          <div className="modal-container_projects">
            {transitions.map(
              ({ item, key, props: style }) =>
                item && <Two style={style} key={key} />
            )}

            {twotransitions.map(
              ({ item, key, props: style }) =>
                item && <One style={style} key={key} />
            )}

            {threetransitions.map(
              ({ item, key, props: style }) =>
                item && <Three style={style} key={key} />
            )}

            {fourtransitions.map(
              ({ item, key, props: style }) =>
                item && <Four style={style} key={key} />
            )}

            {fivetransitions.map(
              ({ item, key, props: style }) =>
                item && <Five style={style} key={key} />
            )}

            {sixtransitions.map(
              ({ item, key, props: style }) =>
                item && <Six style={style} key={key} />
            )}
          </div>
        </StyledProjects>
      </div>
      </>
    );
  }


export default Projects;


