import React from "react";
import { animated } from "react-spring";

const Modal = ({ style }) => (
  <animated.div style={style} className="modal">
    <div className="modal-content">
    <p>
Welcome! I am a purpose-driven developer focused on building meaningful web experiences across a spectrum of different technologies and programming languages. I have a penchant for well-being including nutrition, exercise, communication, and civic volunteering. I equally enjoy collaborating towards a common interest, goal, or task as much as I do finding meaning and accomplishment on an individual level.
    </p>
    <p>
I'm currently available for hire, I encourage you to spend some time browsing my github or projects I've curated below. If you have an opportunity you'd like to discuss I would love to hear from you!
    </p>
    </div>
  </animated.div>
);

export default Modal;